.list-group-item {
  border-left: none;
  border-right: none;
  font-weight: 600;
}

.list-group-item:last-child {
  border-bottom: none;
}

::ng-deep .mat-tooltip {
  background: white !important;
  color: black !important;
  border: 1px solid #ccc !important;
  padding-right: 17px !important;
}

::ng-deep .mat-tooltip.below {
  overflow: initial;
  margin-top: 1rem;
  max-width: 100% !important;
}

::ng-deep .mat-tooltip.below:after {
  top: -0.5rem;
  right: calc(50% - 0.5rem);
  transform: rotate(0);
}

.pdf-part img {
  width: 35px !important;
}

.clin {
  display: none;
}

.underline {
  text-decoration: underline !important;
}

ul.list-group li span:nth-child(2) {
  width: 30%;
}

.patient-history li {
  cursor: pointer;
}

.patient-history li:first-child {
  background-color: #0072ce;
  border-color: #0072ce;
}

.result-detail-wrapper {
  height: 100%;
}

.mobileview {
  display: none;
}

@media screen and (max-width: 767px) {
  .topheadpart p {
    min-width: auto !important;
    display: initial !important;
  }

  .patient-history li {
    display: flex;
    justify-content: space-between;
  }

  .patient-history li .ref {
    width: 65%;
    display: flex;
    align-items: flex-start;
  }

  .patient-history li .ref img {
    margin-top: 5px;
  }

  span.ref span {
    width: 80% !important;
    display: inline-block;
    word-break: break-word;
    padding-left: 10px;
  }

  ul.patient-history li span:nth-child(2) {
    width: 30%;
  }

  .title-container {
    margin: 0px 0 20px 0;
    background: #fff;
    padding: 15px;
    border-top: 2px solid #ccc;
    border-bottom: 2px solid #ccc;
  }

  .result-wrapper {
    padding: 20px 0 0 0 !important;
  }

  .top-card {
    width: 95%;
    padding: 14px 15px;
  }

  mat-card.mat-card {
    width: 95%;
    margin: 0 auto;
    height: auto;
  }

  .page-title {
    padding: 14px 78px 14px 45px;
    font-size: 17px;
  }

  .mobileview {
    display: block;
  }

  .deskview {
    display: none;
  }

  .iconview {
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 0 0 10px 0;
  }

  ::ng-deep .resulticon .mobileview .tooltip.show {
    z-index: 99 !important;
    display: none;
  }
}

.mat-card {
  height: initial;
}

#content {
  padding: 20px 45px 20px 20px;
}

.paddingEq {
  padding: 12x !important;
}

.upper-case {
  text-transform: uppercase !important;
}

.save-change {
  background-color: #f65c00;
  margin-top: 0.5rem;
}

.ph-heading {
  border-bottom: 2px solid #6c757d;
}

::ng-deep .mat-table-sticky {
  top: 0 !important;
}

.loader {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  background: linear-gradient(0deg, rgba(255, 61, 0, 0.2) 33%, #ff3d00 100%);
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}

.loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 157px;
  height: 157px;
  border-radius: 50%;
  background: #fff;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

#logo-inside-loader {
  position: absolute;
  top: 65px;
  left: 55px;
}

.loader-container {
  width: 250px;
  height: 160px;
  position: relative;
  text-align: center;
}

.text-section {
  text-align: center;
  font-size: 15px;
  color: #939393;
  /* #f65c00; */
  font-weight: bold;
  font-family: "gothambook", sans-serif;
}

.g-btn {
  color: #fff;
  background-color: #fd6400;
  border-color: #fd6400;
}

:host ::ng-deep .tooltip-inner {
  /* width: 70% !important; */
  white-space: pre-wrap;
}

.warningTxt p {
  background: #ffefef;
  color: #ce1c52 !important;
  border-radius: 2px;
  margin: 0;
  padding: 10px;
  /* max-width: 725px; */
}

.warningTxt {
  margin-bottom: 15px;
}

.warningTxt p span {
  margin-left: 5px;
}

.warningTxt img {
  width: 20px;
  margin-right: 7px;
}

#content {
  height: calc(100vh - 250px);
  overflow-y: auto;
}